
.item-video-list {
  height: 100%;
  padding-left: 272px;
}
.item-video-list .label {
    color: white;
    margin-right: 15px;
}
.item-video-list .video-table {
    width: 100%;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
}
.item-video-list .video-table tr {
      line-height: 30px;
}
.item-video-list .video-table .head {
      color: white;
}
.item-video-list .video-table .video-td {
      padding: 0 6px;
      width: 160px;
      border-bottom: 1px solid rgba(255, 255, 255, 0.1);
      font-size: 14px;
      text-align: center;
}
.item-video-list .video-dialog {
    width: 650px;
}
.item-video-list .video-dialog .dialog-container {
      font-size: 0;
}
.item-video-list .video-dialog .dialog-container .video-iframe {
        width: 650px;
        height: 360px;
}



