
.vehicle-detail-container {
  height: 100%;
  overflow: hidden;
}
.vehicle-detail-container .operation-holder {
    height: 60px;
    line-height: 60px;
    padding: 0 30px;
    overflow: hidden;
}
.vehicle-detail-container .operation-holder .name {
      font-size: 16px;
      color: #FFFFFF;
      font-weight: bold;
      margin-left: 30px;
}
.vehicle-detail-container .operation-holder .btn-edit {
      position: absolute;
      right: 30px;
      top: 18px;
}
.vehicle-detail-container .operation-holder .btn-report {
      position: absolute;
      right: 100px;
      top: 18px;
}
.vehicle-detail-container .content {
    height: 100%;
}
.vehicle-detail-container .content .content-holder {
      padding: 26px 60px;
      overflow: hidden;
}
.vehicle-detail-container .content .content-holder .btn-div {
        display: inline-block;
        position: absolute;
        right: 80px;
}
.vehicle-detail-container .content .content-holder .vehicle-item {
        overflow: hidden;
        padding-bottom: 20px;
}
.vehicle-detail-container .content .content-holder .content-item {
        width: 50%;
        float: left;
        font-size: 0;
        line-height: 50px;
        overflow: hidden;
}
.vehicle-detail-container .content .content-holder .content-item .title {
          display: inline-block;
          vertical-align: top;
          width: 140px;
          font-size: 14px;
          color: rgba(255, 255, 255, 0.6);
          padding-left: 10px;
}
.vehicle-detail-container .content .content-holder .content-item .desc {
          display: inline-block;
          vertical-align: top;
          width: calc(100% - 140px);
}
.vehicle-detail-container .content .content-holder .content-item .desc span {
            font-size: 14px;
            color: #FFFFFF;
}
.vehicle-detail-container .content .content-holder .content-item .desc span.strong {
              font-weight: bold;
}
.vehicle-detail-container .content .content-holder .content-item .desc .input {
            width: 200px;
}
.vehicle-detail-container .content .content-holder .width {
        overflow: hidden;
        width: 100%;
}
.vehicle-detail-container .content .content-holder .bom-content-holder {
        padding: 10px 20px;
}
.vehicle-detail-container .content .content-holder .bom-item {
        width: 100%;
        overflow: hidden;
}
.vehicle-detail-container .content .content-holder .bom-item .bom-header {
          overflow: hidden;
          line-height: 40px;
}
.vehicle-detail-container .content .content-holder .bom-item .bom-header .bom-title {
            font-size: 14px;
            color: white;
            padding-left: 5px;
}
.vehicle-detail-container .content .content-holder .bom-item .bom-header .btn-replace {
            float: right;
            margin-right: 40px;
}
.vehicle-detail-container .content .content-holder .bom-item .bom-content {
          float: left;
          display: inline-block;
          width: 50%;
          font-size: 0;
          line-height: 30px;
          overflow: hidden;
}
.vehicle-detail-container .content .content-holder .bom-item .bom-content .title {
            display: inline-block;
            vertical-align: top;
            width: 140px;
            font-size: 12px;
            color: rgba(255, 255, 255, 0.6);
            padding-left: 10px;
}
.vehicle-detail-container .content .content-holder .bom-item .bom-content .desc {
            display: inline-block;
            vertical-align: top;
            width: calc(100% - 140px);
}
.vehicle-detail-container .content .content-holder .bom-item .bom-content .desc span {
              word-break: break-all;
              font-size: 12px;
              color: #FFFFFF;
}
.vehicle-detail-container .content .content-holder .bom-item .bom-content .desc span.strong {
                font-weight: bold;
}
.vehicle-detail-container .report-table {
    width: 100%;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
}
.vehicle-detail-container .report-table tr {
      line-height: 30px;
}
.vehicle-detail-container .report-table .head {
      color: white;
}
.vehicle-detail-container .report-table .report-td {
      padding: 0 6px;
      width: 160px;
      border-bottom: 1px solid rgba(255, 255, 255, 0.1);
      font-size: 14px;
      text-align: center;
}
.vehicle-detail-container .print-content-wrap {
    display: none;
}
.vehicle-detail-container .old-sn {
    padding-left: 14px;
}
.vehicle-detail-container .new-password .input .el-input__inner {
    padding-right: 40px;
}



