
.after-pi {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  width: 100%;
  height: 100%;
  /*.table-wrap ::before{*/
  /*display: table;*/
  /*content: '';*/
  /*clear: both;*/
  /*}*/
}
.after-pi .list-form-inline {
    /*position: relative;*/
    /*float:left;*/
    height: 100%;
    line-height: 50px;
}
.after-pi .list-form-inline .el-form-item {
      margin-right: 2px !important;
      margin-bottom: 0;
}
.after-pi .list-form-inline .el-form-item__content {
      width: 180px !important;
}
.after-pi .el-dialog--small .item {
    line-height: 60px;
    height: 60px;
}
.after-pi .el-dialog--small .item .label {
      display: inline-block;
      width: 200px;
}
.after-pi .el-dialog--small .item .el-input {
      width: 300px;
      margin-left: 10px;
}
.after-pi .el-dialog span.required {
    color: #E26829;
    padding-left: 2px;
    display: inline-block;
}



