
@charset "UTF-8";
.battery {
  padding: 44px 0;
  width: 100%;
  height: 100%;
  overflow: auto;
}
.battery .btn-back {
    margin-left: 40px;
    top: -26px;
    position: relative;
}
.battery .battery-content {
    width: 950px;
    margin: 0 auto;
}
.battery .battery-content .connect-tip {
      display: inline-block;
      float: right;
}
.battery .battery-content .connect-tip a {
        color: #E26829;
}
.battery .battery-content .connect-tip .tip-img {
        top: 0;
        right: 20px;
}
.battery .battery-content .title {
      margin-left: 33px;
      font-size: 16px;
      /*display: flex;*/
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}
.battery .battery-content .title img {
        width: 24px;
        height: 24px;
        margin-right: 16px;
}
.battery .battery-content .info {
      overflow: hidden;
      background: #353945;
      width: 950px;
      height: 245px;
      margin-top: 22px;
      /* 老的 Firefox */
      -webkit-box-shadow: 0 1px 0 #2B2F3B;
              box-shadow: 0 1px 0 #2B2F3B;
}
.battery .battery-content .info .part {
        float: left;
        margin-top: 24px;
        width: 474px;
        border-right: 1px dotted #2B2F3B;
}
.battery .battery-content .info .part .item {
          position: relative;
          margin-left: 33px;
          height: 60px;
          /*line-height: 60px;*/
}
.battery .battery-content .info .part .item .label {
            width: 110px;
            display: inline-block;
}
.battery .battery-content .info .part .item .input {
            width: 295px;
}
.battery .battery-content .info .part .item .input-sn:after {
            top: 5px;
}
.battery .battery-content .info .part .btn-row {
          height: 30px;
          width: 100%;
          position: relative;
          line-height: 30px;
          margin-top: -15px;
          margin-bottom: 15px;
}
.battery .battery-content .info .part .btn-row .btn-group {
            position: absolute;
            right: 30px;
}
.battery .battery-content .info .part .long .long-label {
          position: relative;
          top: 0;
          width: 110px;
          display: inline-block;
}
.battery .battery-content .info .part .long .input {
          top: -10px;
}
.battery .battery-content .info .part .sn {
          height: 60px;
          margin-left: 33px;
          line-height: 60px;
          width: 440px;
}
.battery .battery-content .info .part .sn .label {
            width: 90px;
            display: inline-block;
}
.battery .battery-content .info .part .sn .sn-input {
            width: 295px;
}
.battery .battery-content .info .part .sn .btn-read {
            width: 70px;
            margin-left: 7px;
}
.battery .battery-content .info .part .sn .btn-write {
            margin-left: 1px;
            width: 70px;
}
.battery .battery-content .info .right {
        border-right: none;
        font-size: 0;
}
.battery .battery-content .info .right .com-select {
          width: 186px;
}
.battery .battery-content .info .right .tip {
          display: inline-block;
          border-radius: 50%;
          width: 10px;
          height: 10px;
          background: #E26829;
          margin: 0 10px 0 6px;
}
.battery .battery-content .info .right .connect {
          background: #76EE00;
}
.battery .battery-content .info .right .btn-row {
          height: 30px;
          width: 100%;
          position: relative;
          line-height: 30px;
}
.battery .battery-content .info .right .btn-row .btn-group {
            position: absolute;
            right: 30px;
}
.battery .battery-content .info .right .btn-row .btn-group .btns {
              display: inline-block;
}
.battery .battery-content .info .right .btn-row .btn-group .loading {
              display: inline-block;
              vertical-align: middle;
}
.battery .battery-content .data {
      height: auto;
}
.battery .battery-content .after {
      height: auto;
      margin-top: 1px;
}
.battery .battery-content .after .message {
        float: left;
        width: 885px;
        /*position: relative;*/
        margin: 2px 0 0 33px;
        height: 40px;
        line-height: 40px;
}
.battery .battery-content .after .part {
        margin-top: 10px;
}
.battery .battery-content .after .bg {
        background-color: rgba(255, 0, 0, 0.6);
}



