
.upper-attr {
  padding: 5px 0;
}
.upper-attr:first-child {
    padding: 0;
}
.upper-attr .item {
    position: relative;
    height: 50px;
    line-height: 50px;
    font-size: 0;
    overflow: hidden;
}
.upper-attr .item .title {
      display: inline-block;
      padding: 0 20px;
      width: 165px;
      font-size: 12px;
      color: #fff;
      font-weight: 500;
}
.upper-attr .item .desc {
      display: inline-block;
      vertical-align: top;
      width: 400px;
}
.upper-attr .item .desc .el-input {
        width: 310px;
}
.upper-attr .file-item {
    position: relative;
    /*height: 50px;*/
    margin-top: 20px;
    line-height: 30px;
    font-size: 0;
    overflow: hidden;
}
.upper-attr .file-item .title {
      display: inline-block;
      padding: 0 20px;
      width: 165px;
      font-size: 12px;
      color: #fff;
      font-weight: 500;
}
.upper-attr .file-item .desc {
      display: inline-block;
      vertical-align: top;
      width: 400px;
}
.upper-attr .file-item .desc .label {
        display: inline-block;
        width: 110px;
}
.upper-attr .file-item .desc .btn {
        margin-left: 10px;
}
.upper-attr .imgs {
    position: relative;
    height: 50px;
    line-height: 50px;
    font-size: 0;
    overflow: hidden;
}
.upper-attr .imgs .title {
      display: inline-block;
      padding: 0 20px;
      width: 165px;
      font-size: 12px;
      color: #fff;
      font-weight: 500;
}
.upper-attr .imgs .desc {
      display: inline-block;
      vertical-align: top;
      width: 400px;
      overflow: hidden;
}
.upper-attr .imgs .desc .img-upload > div {
        float: left;
        height: 50px;
}
.upper-attr .imgs .desc .img-upload > div .pic {
          width: 50px;
          height: 50px;
          margin-right: 10px;
}
.upper-attr .imgs .desc .img-upload > div .remove {
          width: 15px;
          height: 15px;
          margin-left: 35px;
          position: absolute;
          top: 0;
          z-index: 2;
          background: url("../assets/images/icon-close.png") no-repeat center;
}
.upper-attr .imgs .desc .img-upload > div .remove:hover {
            background: url("../assets/images/icon-close-red.png") no-repeat center;
            width: 15px;
            height: 15px;
}
.upper-attr .imgs .desc .img-upload {
        display: inline-block;
}
.upper-attr .imgs .desc .img-upload .upload-icon {
          width: 50px;
          height: 50px;
}



