
.assembler-index {
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  min-width: 530px;
  min-height: 360px;
}
.assembler-index .operation {
    width: 530px;
}
.assembler-index .operation .item {
      text-align: center;
      display: inline-block;
      width: 230px;
      margin: 15px;
      border-radius: 10px;
      background-color: #98B42C;
      padding-bottom: 20px;
      cursor: pointer;
}
.assembler-index .operation .item .order-list {
        height: 180px;
        background: url("../../assets/images/icon/icon-order.png") no-repeat center;
        background-size: 60px;
}
.assembler-index .operation .item .diagnosis {
        height: 180px;
        background: url("../../assets/images/icon/menu/vehicle.png") no-repeat center;
        background-size: 88px;
}
.assembler-index .operation .item .label {
        margin: auto;
        font-size: 26px;
        font-weight: bold;
}



