
.componentSave-list {
  width: 100%;
  height: 100%;
}
.componentSave-list .head {
    height: 60px;
    border-bottom: 1px solid #2B2F3B;
    overflow: hidden;
    line-height: 60px;
    font-size: 14px;
    color: #A1A5B1;
    font-weight: bold;
}
.componentSave-list .head .headTitle {
      float: left;
      position: relative;
      padding-left: 20px;
}
.componentSave-list .head .date {
      float: right;
      position: relative;
      padding-right: 20px;
}
.componentSave-list .td {
    overflow: hidden;
    text-overflow: ellipsis;
}
.componentSave-list .name {
    max-width: 120px;
}
.componentSave-list .address {
    max-width: 280px;
}



