
@charset "UTF-8";
.hmi {
  width: 100%;
  height: 100%;
  overflow: auto;
  padding: 44px 0;
}
.hmi .btn-back {
    margin-left: 40px;
    top: -26px;
    position: relative;
}
.hmi .hmi-content {
    width: 950px;
    margin: 0 auto;
}
.hmi .hmi-content .connect-tip {
      display: inline-block;
      float: right;
}
.hmi .hmi-content .connect-tip a {
        color: #E26829;
}
.hmi .hmi-content .connect-tip .tip-img {
        top: 0;
        right: 20px;
}
.hmi .hmi-content .title {
      margin-left: 33px;
      font-size: 16px;
      /*display: flex;*/
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}
.hmi .hmi-content .title img {
        width: 24px;
        height: 24px;
        margin-right: 16px;
}
.hmi .hmi-content .info {
      overflow: hidden;
      background: #353945;
      width: 950px;
      /*min-height: 380px;*/
      margin-top: 22px;
      /* 老的 Firefox */
      -webkit-box-shadow: 0 1px 0 #2B2F3B;
              box-shadow: 0 1px 0 #2B2F3B;
}
.hmi .hmi-content .info .btn-row {
        height: 30px;
        width: 100%;
        position: relative;
        line-height: 30px;
}
.hmi .hmi-content .info .border-bottom {
        border-bottom: 1px solid #2B2F3B;
}
.hmi .hmi-content .info .info-left {
        float: left;
        margin-top: 9px;
        width: 474px;
        border-right: 1px dotted #2B2F3B;
}
.hmi .hmi-content .info .info-left .item {
          margin-left: 33px;
          height: 60px;
          line-height: 60px;
}
.hmi .hmi-content .info .info-left .item .label {
            width: 110px;
            display: inline-block;
            line-height: 20px;
            vertical-align: middle;
            word-wrap: break-word;
}
.hmi .hmi-content .info .info-left .item .input {
            width: 295px;
}
.hmi .hmi-content .info .info-left .item .input > input {
              height: 29px;
}
.hmi .hmi-content .info .info-left .item .el-select {
            width: 290px;
}
.hmi .hmi-content .info .info-left .btn-group {
          position: absolute;
          right: 40px;
}
.hmi .hmi-content .info .info-left .sn {
          height: 60px;
          margin-left: 33px;
          line-height: 60px;
          width: 410px;
}
.hmi .hmi-content .info .info-left .sn .label {
            width: 90px;
            display: inline-block;
}
.hmi .hmi-content .info .info-left .sn .sn-input {
            width: 150px;
            margin-left: 10px;
}
.hmi .hmi-content .info .info-left .sn .btn-read {
            width: 70px;
            margin-left: 7px;
}
.hmi .hmi-content .info .info-left .sn .btn-write {
            margin-left: 1px;
            height: 29px;
            width: 70px;
}
.hmi .hmi-content .info .info-right {
        float: left;
        margin-top: 9px;
        width: 474px;
}
.hmi .hmi-content .info .info-right .item {
          margin-left: 33px;
          height: 60px;
          line-height: 60px;
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          -webkit-box-align: center;
              -ms-flex-align: center;
                  align-items: center;
}
.hmi .hmi-content .info .info-right .item .el-select {
            width: 187px;
}
.hmi .hmi-content .info .info-right .item .tip {
            display: inline-block;
            border-radius: 50%;
            width: 10px;
            height: 10px;
            background: #E26829;
            margin: 0 6px;
}
.hmi .hmi-content .info .info-right .item .connect {
            background: #76EE00;
}
.hmi .hmi-content .info .info-right .item .label {
            width: 110px;
            display: inline-block;
            line-height: 20px;
            vertical-align: middle;
            word-wrap: break-word;
}
.hmi .hmi-content .info .info-right .item .input {
            width: 295px;
}
.hmi .hmi-content .info .info-right .item > button {
            margin-right: 12px;
}
.hmi .hmi-content .info .info-right .btn-group {
          position: absolute;
          right: 26px;
}
.hmi .hmi-content .info .info-right .btns {
          display: inline-block;
}
.hmi .hmi-content .info .info-right .loading {
          display: inline-block;
          vertical-align: middle;
}
.hmi .hmi-content .data {
      height: 100%;
      margin-top: 1px;
}
.hmi .hmi-content .record {
      height: 80px;
      line-height: 20px;
}
.hmi .hmi-content .record .wrap {
        padding: 20px 33px;
}
.hmi .hmi-content .record .wrap .title-record {
          font-size: 16px;
}
.hmi .hmi-content .record .wrap .number {
          color: #E26829;
}
.hmi .hmi-content .tip-dialog .el-dialog__body {
      padding: 0;
}



