
.after-pi-edit .content-wrap .content {
  width: 1200px;
}
.after-pi-edit .content-wrap .table-content {
  padding-bottom: 10px;
}
.after-pi-edit .content {
  /*height: 100%;*/
  border: 1px solid #2B2F3B;
  margin-bottom: 10px;
}
.after-pi-edit .content .order-info {
    width: 100%;
}
.after-pi-edit .content .order-info .title {
      height: 50px;
      line-height: 50px;
      padding-left: 40px;
      /*font-size: 12px;*/
}
.after-pi-edit .content .order-info .left {
      float: left;
      width: 100%;
      background: #353945;
      -webkit-box-shadow: 0 -1px #2B2F3B;
              box-shadow: 0 -1px #2B2F3B;
}
.after-pi-edit .content .order-info .left .col-left {
        float: left;
        margin-top: 9px;
        width: 474px;
}
.after-pi-edit .content .order-info .left .col-left .el-date-editor {
          width: 256px;
          margin-left: 10px;
}
.after-pi-edit .content .order-info .left .col-right {
        float: left;
        margin-top: 9px;
        width: 474px;
}
.after-pi-edit .content .order-info .left .col-right .el-date-editor {
          width: 256px;
          margin-left: 10px;
}
.after-pi-edit .content .order-info .left .note {
        margin-bottom: 5px;
}
.after-pi-edit .content .order-info .left .item {
        margin: 2px 0 0 40px;
        line-height: 60px;
        overflow: hidden;
        height: 50px;
        clear: both;
}
.after-pi-edit .content .order-info .left .item .label {
          display: inline-block;
          width: 110px;
}
.after-pi-edit .content .order-info .left .item .input {
          margin-left: 10px;
          width: 256px;
          border-radius: 0 0 10px 10px;
}
.after-pi-edit .content .order-info .left .item .input-note {
          margin-left: 10px;
          width: 726px;
}
.after-pi-edit .content .order-info .parts-list {
      float: left;
      padding: 0 40px;
      font-size: 12px;
      overflow: hidden;
      height: 50px;
      line-height: 50px;
      width: 100%;
      -webkit-box-shadow: 0 -1px #2B2F3B;
              box-shadow: 0 -1px #2B2F3B;
}
.after-pi-edit .content .order-info .parts-list .labelList {
        /*font-size: 12px;*/
}
.after-pi-edit .content .order-info .parts-list .att-btn {
        margin-left: 46px;
        padding: 6px 9px;
        min-width: 0;
}
.after-pi-edit .content .order-info .parts-list .labelAdd {
        margin-left: 14px;
        /*font-size: 12px;*/
}
.after-pi-edit .content .order-info .table-wrap .long-td {
      max-width: 270px;
      text-overflow: ellipsis;
      overflow: hidden;
      text-align: left;
}
.after-pi-edit .content .order-info .footer {
      width: 100%;
      height: 60px;
      line-height: 60px;
      -webkit-box-shadow: 0 -1px #2B2F3B;
              box-shadow: 0 -1px #2B2F3B;
}
.after-pi-edit .content .order-info .footer .footer-pagination {
        /*float: right;*/
}
.after-pi-edit .content .order-info .footer .btn-left {
        display: inline-block;
        width: 100px;
        float: left;
        padding-left: 40px;
}
.after-pi-edit .content .order-info .footer .btn-right {
        display: inline-block;
        float: right;
        width: 100px;
        margin-right: 60px;
}
.after-pi-edit .height1 {
  height: 272px;
}
.after-pi-edit .height2 {
  height: 372px;
  -webkit-box-shadow: 1px 0 0 1px #2B2F3B;
          box-shadow: 1px 0 0 1px #2B2F3B;
}
.after-pi-edit .dialog .el-dialog--large {
  width: 1100px;
}
.after-pi-edit .dialog .header .search-content {
  width: 200px;
  margin-right: 10px;
}
.after-pi-edit .dialog .header .search-btn {
  position: absolute;
  top: 15px;
  left: 250px;
  width: 30px;
  height: 30px;
}
.print-pi-wrap {
  display: none;
}
.print-pi-wrap .printHead h2, .print-pi-wrap .printHead h3 {
    text-align: center;
}
.print-pi-wrap .printHead h2 {
    font-size: 24px;
}
.print-pi-wrap .printHead h3 {
    font-size: 14px;
}
.print-pi-wrap .printHead .dis_left {
    float: left;
}
.print-pi-wrap .printHead .dis_right {
    float: right;
}
.print-pi-wrap .printHead .dis_right label {
      width: 120px;
}
.print-pi-wrap .printHead .dis_right span {
      width: 200px;
      text-align: center;
}
.print-pi-wrap .printHead label {
    display: inline-block;
    /*text-align: center;*/
}



