
.after-pi-edit .content {
  /*height: 100%;*/
  /*overflow: auto;*/
  border: 1px solid #2B2F3B;
}
.after-pi-edit .content .order-info {
    /*height: 272px;*/
}
.after-pi-edit .content .order-info .title {
      height: 50px;
      line-height: 50px;
      padding-left: 40px;
      /*font-size: 12px;*/
}
.after-pi-edit .content .order-info .left {
      float: left;
      width: 100%;
      background: #353945;
      -webkit-box-shadow: 0 -1px #2B2F3B;
              box-shadow: 0 -1px #2B2F3B;
}
.after-pi-edit .content .order-info .left .item {
        margin-left: 40px;
        line-height: 60px;
        overflow: hidden;
        height: 50px;
}
.after-pi-edit .content .order-info .left .item .label {
          display: inline-block;
          width: 156px;
}
.after-pi-edit .content .order-info .left .info-list {
        font-size: 0;
}
.after-pi-edit .content .order-info .left .info-list .col-left {
          display: inline-block;
          width: 50%;
}
.after-pi-edit .content .order-info .left .info-list .col-right {
          display: inline-block;
          width: 50%;
}
.after-pi-edit .content .order-info .left .info-list .node {
          width: 100%;
}
.after-pi-edit .content .order-info .left .info-list .node .label {
            width: 40%;
}
.after-pi-edit .content .order-info .left .info-list .list-group {
          padding: 0 40px;
}
.after-pi-edit .content .order-info .left .info-list .list-group .list-group-item {
            height: 50px;
            line-height: 50px;
            font-size: 0;
}
.after-pi-edit .content .order-info .left .info-list .list-group .list-group-item span {
              display: inline-block;
}
.after-pi-edit .content .order-info .left .info-list .list-group .list-group-item span:first-child {
              /*font-size: 12px;*/
              color: #FFFFFF;
              width: 40%;
}
.after-pi-edit .content .order-info .left .info-list .list-group .list-group-item span:last-child {
              /*font-size: 12px;*/
              color: #A1A5B1;
              width: 60%;
}
.after-pi-edit .content .order-info .parts-list {
      float: left;
      padding: 27px 40px;
      /*font-size: 12px;*/
      overflow: hidden;
      height: 50px;
      width: 100%;
      -webkit-box-shadow: 0 -1px #2B2F3B;
              box-shadow: 0 -1px #2B2F3B;
}
.after-pi-edit .content .order-info .parts-list .label1 {
        display: inline-block;
        /*font-size: 12px;*/
}
.after-pi-edit .content .order-info .table-wrap {
      -webkit-box-shadow: 0 -1px #2B2F3B;
              box-shadow: 0 -1px #2B2F3B;
      overflow-x: hidden;
}
.after-pi-edit .content .order-info .table-wrap .long-td {
        max-width: 200px;
        text-overflow: ellipsis;
        overflow: hidden;
}
.after-pi-edit .content .order-info .table-wrap .table-content {
        padding-bottom: 0;
}
.after-pi-edit .content .order-info .footer {
      width: 100%;
      height: 50px;
      padding: 10px 38px;
      clear: both;
}
.after-pi-edit .content .order-info .footer .footer-pagination {
        float: right;
}
.after-pi-edit .content .order-info .footer .footer-delete {
        margin-top: 3px;
        background-color: #858890;
}
.print-pi-wrap {
  display: none;
}
.print-pi-wrap .printHead h2, .print-pi-wrap .printHead h3 {
    text-align: center;
}
.print-pi-wrap .printHead h2 {
    font-size: 24px;
}
.print-pi-wrap .printHead h3 {
    font-size: 14px;
}
.print-pi-wrap .printHead .dis_left {
    float: left;
}
.print-pi-wrap .printHead .dis_right {
    float: right;
}
.print-pi-wrap .printHead .dis_right label {
      width: 120px;
}
.print-pi-wrap .printHead .dis_right span {
      width: 200px;
      text-align: center;
}
.print-pi-wrap .printHead label {
    display: inline-block;
    /*text-align: center;*/
}



