
.product-detail-container {
  height: 100%;
  overflow: hidden;
  padding-left: 272px;
}
.product-detail-container .content-holder {
    height: calc(100% - 60px);
    overflow: auto;
}
.product-detail-container .content-holder .thumb-img-box {
      width: 100%;
      height: 72px;
      font-size: 0;
      text-align: center;
}
.product-detail-container .content-holder .thumb-img-box .thumb {
        display: inline-block;
        width: 72px;
        height: 72px;
        margin-right: 10px;
        /*background: url('../../../assets/images/components/carousel-example@2x.png');*/
        background-size: cover;
        border: 1px solid rgba(39, 43, 55, 0.2);
        cursor: pointer;
        overflow: hidden;
}
.product-detail-container .content-holder .thumb-img-box .thumb .thumb-img {
          width: 70px;
          height: 70px;
}
.product-detail-container .content-holder .thumb-img-box .thumb:last-child {
          margin-right: 0;
}
.product-detail-container .content-holder .thumb-img-box .thumb.selected {
          border: 2px solid #e26829;
}
.product-detail-container .content-holder .content-box {
      padding: 26px 60px;
}
.product-detail-container .content-holder .content-box .content-item {
        padding: 16px 0;
        border-top: 1px solid rgba(255, 255, 255, 0.1);
        font-size: 0;
}
.product-detail-container .content-holder .content-box .content-item.border-none {
          border: none;
          padding-top: 0;
}
.product-detail-container .content-holder .content-box .content-item .title {
          display: inline-block;
          vertical-align: top;
          width: 140px;
          font-size: 14px;
          color: rgba(255, 255, 255, 0.6);
          padding: 0 5px;
}
.product-detail-container .content-holder .content-box .content-item .title.strong {
            color: #FFFFFF;
            font-weight: bold;
}
.product-detail-container .content-holder .content-box .content-item .desc {
          display: inline-block;
          vertical-align: top;
          width: calc(100% - 140px);
}
.product-detail-container .content-holder .content-box .content-item .desc.h-24 {
            height: 24px;
}
.product-detail-container .content-holder .content-box .content-item .desc span {
            font-size: 14px;
            color: #FFFFFF;
}
.product-detail-container .content-holder .content-box .content-item .desc span.strong {
              font-weight: bold;
}
.product-detail-container .content-holder .content-box .extra-div {
        padding: 16px 0;
        border-top: 1px solid rgba(255, 255, 255, 0.1);
}
.product-detail-container .content-holder .content-box .extra-div .title {
          display: inline-block;
          vertical-align: top;
          width: 140px;
          font-size: 14px;
          color: rgba(255, 255, 255, 0.6);
          padding: 0 5px;
}
.product-detail-container .content-holder .content-box .extra-div .extra .desc {
          display: inline-block;
          vertical-align: top;
          position: relative;
          top: -16px;
}
.product-detail-container .content-holder .content-box .extra-div .extra .desc .fileContent {
            margin-top: 16px;
}
.product-detail-container .content-holder .content-box .extra-div .extra .desc .fileContent .title {
              display: inline-block;
}
.product-detail-container .content-holder .content-box .extra-div .extra .desc .fileContent .btn-group {
              display: inline-block;
              margin-left: 6px;
}
.product-detail-container .content-holder .content-box .extra-div .extra .img-group {
          margin-top: 10px;
}
.product-detail-container .content-holder .content-box .extra-div .extra .img-group img {
            width: 50px;
            height: 50px;
}
.product-detail-container .content-holder .content-box .extra-div .extra .text {
          top: 0;
}
.product-detail-container .content-holder .content-box .version .version-label {
        display: inline-block;
        margin-right: 40px;
}
.product-detail-container .history-table {
    width: 100%;
    /*min-width:700px;*/
    border-top: 1px solid rgba(255, 255, 255, 0.1);
}
.product-detail-container .history-table tr {
      line-height: 30px;
}
.product-detail-container .history-table .head {
      color: white;
}
.product-detail-container .history-table .history-td {
      padding: 0 6px;
      width: 160px;
      border-bottom: 1px solid rgba(255, 255, 255, 0.1);
      font-size: 14px;
      text-align: center;
}
.product-detail-container .upload-label {
    display: inline-block;
    width: 100px;
    vertical-align: top;
    padding-left: 20px;
}
.product-detail-container .HWUpload {
    display: inline-block;
}
.product-detail-container .remark {
    width: calc(100% - 140px);
}
.product-detail-container .head-cell-class {
    font-size: 12px;
}
.product-detail-container .spec-list-holder {
    overflow: auto;
    width: 100%;
    max-height: 600px;
}



