
.material-list {
  /*display:flex;*/
  /*flex-direction:column;*/
  width: 100%;
  height: 100%;
  overflow: auto;
  /*.table-wrap{*/
  /*!*flex:1;*!*/
  /*.table-content-holder{*/
  /*background:#383d49;*/
  /*}*/
  /*.table-content tr td{*/
  /*font-size:18px;*/
  /*}*/
  /*}*/
}
.material-list .el-form-item {
    height: 100%;
    line-height: 50px;
    display: inline-block;
    margin-bottom: 0px;
}
.material-list .el-form-item .el-form-item__label {
      font-size: 18px;
}
.material-list .el-form-item .el-input {
      width: 200px;
}
.material-list .loading {
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.material-list .table-wrap {
    /*flex:1;*/
    width: 100%;
    height: calc(100% - 60px);
    overflow: auto;
}
.material-list .table-wrap .table-content-holder-m {
      overflow: auto;
      width: 100%;
      height: calc(100% - 60px);
      background: #383d49;
}
.material-list .table-wrap .table-content-m {
      width: 100%;
}
.material-list .table-wrap .table-content-m tr {
        height: 50px;
        cursor: pointer;
}
.material-list .table-wrap .table-content-m tr td {
          padding: 0px 10px;
          text-align: left;
          white-space: nowrap;
          font-size: 18px;
          border-bottom: 1px solid #2B2F3B;
}
.material-list .table-wrap .table-content-m tr td a {
            font-size: 18px;
}
.material-list .table-wrap .table-content-m tr .long-td {
          max-width: 270px;
          text-overflow: ellipsis;
          overflow: hidden;
          text-align: left;
}
.material-list .table-wrap .table-content-m tr .al-right {
          text-align: right;
}
.material-list html:-webkit-full-screen {
    background: #383d49;
}
.material-list html:-moz-full-screen {
    background: #383d49;
}
.material-list html:-ms-fullscreen {
    background: #383d49;
}
.material-list html:fullscreen {
    background: #383d49;
}
.input-holder {
  overflow: hidden;
}
.input-holder > label {
    float: left;
    display: inline-block;
    width: 25px;
}
.input-holder .el-input {
    float: left;
    width: calc(100% - 30px);
}
.bg {
  background-color: red;
}
.el-dialog--tiny .item {
  height: 60px;
}
.el-dialog--tiny .item .label {
    display: inline-block;
    width: 100px;
}
.el-dialog--tiny .item .el-input {
    width: 220px;
}



