
.after-pa-edit .content {
  /*height: 100%;*/
  border: 1px solid #2B2F3B;
  margin-bottom: 10px;
}
.after-pa-edit .content .order-info {
    width: 100%;
}
.after-pa-edit .content .order-info .title {
      height: 50px;
      line-height: 50px;
      padding-left: 40px;
      /*font-size: 12px;*/
}
.after-pa-edit .content .order-info .left {
      float: left;
      width: 100%;
      background: #353945;
      -webkit-box-shadow: 0 -1px #2B2F3B;
              box-shadow: 0 -1px #2B2F3B;
}
.after-pa-edit .content .order-info .left .col-left {
        float: left;
        margin-top: 9px;
