        float: left;
        padding-left: 40px;
}
.after-pa-edit .content .order-info .footer .btn-right {
        display: inline-block;
        float: right;
        width: 100px;
        margin-right: 60px;
}
.after-pa-edit .height1 {
  height: 272px;
}
.after-pa-edit .height2 {
  height: 372px;
}
.after-pa-edit .dialog .el-dialog--large {
  width: 1100px;
}
.after-pa-edit .dialog .header .search-content {
  width: 200px;
  margin-right: 10px;
}
.after-pa-edit .dialog .header .search-btn {
  position: absolute;
  top: 15px;
  left: 250px;
  width: 30px;
  height: 30px;
}



