
@charset "UTF-8";
.controller-container {
  width: 100%;
  height: 100%;
  overflow: auto;
  padding: 44px 0;
}
.controller-container .btn-back {
    margin-left: 40px;
    top: -26px;
    position: relative;
}
.controller-container .controller-content {
    width: 950px;
    margin: 0 auto;
}
.controller-container .controller-content .connect-tip {
      display: inline-block;
      float: right;
}
.controller-container .controller-content .connect-tip a {
        color: #E26829;
}
.controller-container .controller-content .connect-tip .tip-img {
        top: 0;
        right: 20px;
}
.controller-container .controller-content .title {
      margin-left: 33px;
      font-size: 16px;
      /*display: flex;*/
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}
.controller-container .controller-content .title img {
        width: 24px;
        height: 24px;
        margin-right: 16px;
}
.controller-container .controller-content .info {
      overflow: hidden;
      background: #353945;
      width: 950px;
      min-height: 350px;
      margin-top: 22px;
      /* 老的 Firefox */
      -webkit-box-shadow: 0 1px 0 #2B2F3B;
              box-shadow: 0 1px 0 #2B2F3B;
}
.controller-container .controller-content .info .part {
        float: left;
        margin-top: 9px;
        width: 473px;
        border-right: 1px dotted #2B2F3B;
}
.controller-container .controller-content .info .part .item {
          overflow: hidden;
          margin-left: 33px;
          height: 60px;
          line-height: 60px;
}
.controller-container .controller-content .info .part .item .label {
            width: 110px;
            display: inline-block;
            line-height: 20px;
            word-wrap: break-word;
}
.controller-container .controller-content .info .part .item .input {
            width: 295px;
}
.controller-container .controller-content .info .part .com {
          height: 60px;
          margin-left: 33px;
          line-height: 60px;
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          -webkit-box-align: center;
              -ms-flex-align: center;
                  align-items: center;
          padding-right: 28px;
}
.controller-container .controller-content .info .part .com .label {
            width: 110px;
            display: inline-block;
}
.controller-container .controller-content .info .part .com .tip {
            display: inline-block;
            border-radius: 50%;
            width: 10px;
            height: 10px;
            background: #E26829;
            margin: 0 6px;
}
.controller-container .controller-content .info .part .com .connect {
            background: #76EE00;
}
.controller-container .controller-content .info .part .com .com-select {
            -webkit-box-flex: 1;
                -ms-flex: 1;
                    flex: 1;
            margin-left: 10px;
}
.controller-container .controller-content .info .part .sn {
          height: 60px;
          margin-left: 33px;
          line-height: 60px;
          width: 440px;
}
.controller-container .controller-content .info .part .sn .label {
            width: 110px;
            display: inline-block;
}
.controller-container .controller-content .info .part .sn .sn-input {
            width: 295px;
}
.controller-container .controller-content .info .part .sn .btn-write {
            margin-left: 4px;
}
.controller-container .controller-content .info .btn-row {
        height: 30px;
        width: 100%;
        position: relative;
        line-height: 30px;
}
.controller-container .controller-content .info .btn-row .btn-group {
          position: absolute;
          right: 0;
}
.controller-container .controller-content .info .btn-row .loading {
          display: inline-block;
          vertical-align: middle;
          margin-top: -13px;
}
.controller-container .controller-content .info .right {
        border-right: none;
}
.controller-container .controller-content .can-data {
      margin-top: 1px;
}
.controller-container .controller-content .data {
      width: 950px;
      height: 100%;
      background: #353945;
      margin-top: 1px;
}
.controller-container .controller-content .data .part {
        display: inline-block;
        margin-top: 9px;
        width: 440px;
        margin-left: 32px;
        border-right: 1px dotted #2B2F3B;
}
.controller-container .controller-content .data .part .head {
          height: 60px;
          line-height: 60px;
}
.controller-container .controller-content .data .part .head .label {
            display: inline-block;
            width: 110px;
            line-height: 20px;
            vertical-align: middle;
}
.controller-container .controller-content .data .part .head .input {
            margin-left: 5px;
            width: 296px;
            position: relative;
}
.controller-container .controller-content .data .part .text {
          margin-left: 112px;
          height: 40px;
          line-height: 40px;
}
.controller-container .controller-content .data .part .text .label {
            color: #A1A5B1;
            width: 148px;
            line-height: 20px;
            word-wrap: break-word;
            display: inline-block;
            vertical-align: middle;
            text-align: center;
}
.controller-container .controller-content .data .part .content .item {
          height: 60px;
          line-height: 60px;
}
.controller-container .controller-content .data .part .content .item .label {
            display: inline-block;
            width: 110px;
            word-wrap: break-word;
            line-height: 20px;
            vertical-align: middle;
}
.controller-container .controller-content .data .part .content .item .input-left {
            margin-left: 5px;
            width: 148px;
}
.controller-container .controller-content .data .part .content .item .input-left > input {
              border-radius: 20px 0 0 20px;
}
.controller-container .controller-content .data .part .content .item .input-right {
            width: 148px;
}
.controller-container .controller-content .data .part .content .item .input-right > input {
              border-radius: 0 20px 20px 0;
}
.controller-container .controller-content .data .right {
        border-right: none;
}
.controller-container .controller-content .btn-group {
      float: right;
      height: 30px;
      line-height: 30px;
      margin-right: 30px;
}



