
.diagnosis-container {
  font-size: 16px;
}
.diagnosis-container .header.top-box .label-com {
    margin-left: 20px;
    margin-right: 10px;
}
.diagnosis-container .header.top-box .btn-connect {
    margin: 0 8px;
}
.diagnosis-container .header.top-box .tip {
    display: inline-block;
    border-radius: 50%;
    width: 10px;
    height: 10px;
    background: #E26829;
}
.diagnosis-container .header.top-box .connect {
    background: #76EE00;
}
.diagnosis-container .header.top-box .tip-img {
    width: 18px;
    position: relative;
    top: 4px;
}
.diagnosis-container .content-wrap {
    padding: 10px 0;
}
.diagnosis-container .content-wrap .content {
      padding-left: 0;
}
.diagnosis-container .content-wrap .content .el-collapse-item__header {
        font-size: 0;
}
.diagnosis-container .content-wrap .content .info .search-item {
        width: 100%;
        font-size: 0;
        padding: 15px 50px;
}
.diagnosis-container .content-wrap .content .info .search-item .tip-img {
          width: 18px;
          position: relative;
          top: 4px;
          margin-left: 14px;
}
.diagnosis-container .content-wrap .content .info .search-item .label {
          display: inline-block;
          width: 100px;
}
.diagnosis-container .content-wrap .content .info .search-item .scan-input {
          width: 240px;
}
.diagnosis-container .content-wrap .content .info .search-item .scan-input .el-input__inner {
            padding-right: 40px;
            text-align: right;
}
.diagnosis-container .content-wrap .content .info .search-item .search-btn {
          border-radius: 100px;
          position: relative;
          margin-left: 15px;
}
.diagnosis-container .content-wrap .content .info .title-select {
        position: absolute;
        right: 120px;
        top: 16px;
}
.diagnosis-container .content-wrap .content .info .title-loading {
        position: absolute;
        display: inline-block;
        width: 30px;
        height: 30px;
        margin-top: 15px;
}
.diagnosis-container .content-wrap .content .info .icon {
        display: inline-block;
        width: 20px;
        height: 20px;
        /*line-height: 20px;*/
        vertical-align: middle;
        position: relative;
        top: -4px;
}
.diagnosis-container .content-wrap .content .info .title-text {
        display: inline-block;
        padding-left: 10px;
        width: 100px;
}
.diagnosis-container .content-wrap .content .info .title-solution {
        display: inline-block;
        margin-left: 5px;
}
.diagnosis-container .content-wrap .content .info .title-sn {
        display: inline-block;
        width: 260px;
}
.diagnosis-container .content-wrap .content .info .title-flag {
        display: inline-block;
        /*width: 60px;*/
}
.diagnosis-container .content-wrap .content .info .title-progress {
        display: inline-block;
        width: 120px;
        margin-left: 50px;
}
.diagnosis-container .content-wrap .content .info > .title {
        position: relative;
        padding-left: 50px;
}
.diagnosis-container .content-wrap .content .info > .title li {
          display: inline-block;
          font-size: 14px;
          line-height: 60px;
}
.diagnosis-container .content-wrap .content .info > .title li:nth-child(1) {
            width: 100px;
}
.diagnosis-container .content-wrap .content .info > .title li:nth-child(2) {
            width: 250px;
}
.diagnosis-container .content-wrap .content .info > .title li:nth-child(3) {
            width: 296px;
}
.diagnosis-container .content-wrap .content .info > .title li:nth-child(4) {
            width: 160px;
}
.diagnosis-container .content-wrap .content .info > .title .service {
          position: absolute;
          right: 0;
          top: 50%;
          margin-top: -13px;
}
.diagnosis-container .content-wrap .content .info .info-list-box {
        border-top: 1px solid #22252E;
        border-bottom: 1px solid #22252E;
        padding: 20px 30px;
}
.diagnosis-container .content-wrap .content .info .info-list-box .header-title {
          font-size: 14px;
          color: #858890;
          margin-bottom: 6px;
}
.diagnosis-container .content-wrap .content .info .info-list-box .info-list {
          font-size: 0;
}
.diagnosis-container .content-wrap .content .info .info-list-box .info-list .col-left {
            display: inline-block;
            width: 50%;
            text-align: left;
}
.diagnosis-container .content-wrap .content .info .info-list-box .info-list .col-right {
            display: inline-block;
            width: 50%;
            text-align: right;
}
.diagnosis-container .content-wrap .content .info .info-list-box .info-list .list-group {
            text-align: left;
            margin-top: 20px;
}
.diagnosis-container .content-wrap .content .info .info-list-box .info-list .list-group .list-group-item {
              height: 60px;
              line-height: 60px;
              font-size: 0;
              border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}
.diagnosis-container .content-wrap .content .info .info-list-box .info-list .list-group .list-group-item span {
                display: inline-block;
}
.diagnosis-container .content-wrap .content .info .info-list-box .info-list .list-group .list-group-item span:first-child {
                font-size: 14px;
                color: #FFFFFF;
                width: 40%;
}
.diagnosis-container .content-wrap .content .info .info-list-box .info-list .list-group .list-group-item span:last-child {
                font-size: 14px;
                color: #A1A5B1;
                width: 60%;
}
.diagnosis-container .content-wrap .content .info .info-list-box .info-list .list-group .list-group-item {
              display: -webkit-box;
              display: -ms-flexbox;
              display: flex;
              -webkit-box-align: center;
                  -ms-flex-align: center;
                      align-items: center;
              line-height: 24px;
              border-top: 1px solid rgba(255, 255, 255, 0.1);
              border-bottom: none;
}
.diagnosis-container .content-wrap .content .info .info-list-box .info-list .list-group .list-group-item span:first-child {
                font-size: 14px;
                color: #FFFFFF;
                width: 157px;
}
.diagnosis-container .content-wrap .content .info .info-list-box .info-list .list-group .list-group-item span:last-child {
                font-size: 14px;
                color: #A1A5B1;
                -webkit-box-flex: 1;
                    -ms-flex: 1;
                        flex: 1;
}
.diagnosis-container .content-wrap .content .info .info-list-box .info-list .list-group.list-all span:first-child {
              width: 157px;
}
.diagnosis-container .content-wrap .content .info .input-sn {
        width: 240px;
        height: 30px;
        margin-right: 10px;
}
.diagnosis-container .content-wrap .content .info .input-sn .el-input__inner {
          padding-right: 40px;
          text-align: right;
}
.diagnosis-container .content-wrap .content .test {
        overflow: hidden;
        text-align: center;
}
.diagnosis-container .content-wrap .content .test .test-btn {
          margin-bottom: 10px;
          display: inline-block;
          font-size: 24px;
          height: 50px;
          width: 250px;
          border-radius: 25px;
          background-color: #A1A5B1;
          cursor: pointer;
          line-height: 50px;
          font-weight: bold;
          /*&:hover {*/
          /*background-color: #E26829;*/
          /*}*/
}
.diagnosis-container .content-wrap .content .test .test-btn.active {
          background-color: #E26829;
}
.diagnosis-container .dialog-container-pic {
    text-align: center;
}
.diagnosis-container .dialog-container-pic .img {
      width: 100%;
      height: auto;
}
.diagnosis-container .error-shooting .content-item {
    width: 100%;
    padding: 16px 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    font-size: 0;
}
.diagnosis-container .error-shooting .content-item .title {
      display: inline-block;
      vertical-align: top;
      width: 140px;
      font-size: 14px;
      color: rgba(255, 255, 255, 0.6);
      padding: 0 5px;
}
.diagnosis-container .error-shooting .content-item .desc {
      display: inline-block;
      vertical-align: top;
      width: calc(100% - 140px);
}
.diagnosis-container .error-shooting .content-item .desc span {
        font-size: 14px;
        color: #FFFFFF;
}
.diagnosis-container .error-shooting .content-item .desc span.strong {
          font-weight: bold;
}
.diagnosis-container .error-shooting .content-item .desc p {
        font-size: 14px;
        color: #FFFFFF;
}
.diagnosis-container .input {
    margin-left: 20px;
    width: 260px;
}
.diagnosis-container .print-content-wrap {
    display: none;
}
.diagnosis-container .tip-dialog .el-dialog__body {
    padding: 0;
}



