
.after-package {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  width: 100%;
  height: 100%;
}
.after-package .list-form-inline {
    height: 40px;
    line-height: 40px;
    display: inline-block;
}
.after-package .list-form-inline .el-form-item {
      margin-right: 2px !important;
      margin-bottom: 0;
}
.after-package .list-form-inline .el-form-item__content {
      width: 180px !important;
}
.after-package .el-dialog--small .item {
    line-height: 60px;
    height: 60px;
}
.after-package .el-dialog--small .item .label {
      display: inline-block;
      width: 200px;
}
.after-package .el-dialog--small .item .el-input {
      width: 300px;
      margin-left: 10px;
}
.after-package .el-dialog {
    padding-left: 2px;
}
.after-package .el-dialog span.required {
      color: #E26829;
      padding-left: 2px;
      display: inline-block;
}



