
.model-detail-container {
  height: 100%;
  overflow: hidden;
}
.model-detail-container .operation-holder {
    height: 60px;
    line-height: 60px;
    padding: 0 30px;
    overflow: hidden;
}
.model-detail-container .operation-holder .name {
      font-size: 16px;
      color: #FFFFFF;
      font-weight: bold;
      margin-left: 30px;
}
.model-detail-container .content-holder {
    height: calc(100% - 60px);
    overflow: auto;
}
.model-detail-container .content-holder .thumb-img-box {
      width: 100%;
      height: 72px;
      font-size: 0;
      text-align: center;
}
.model-detail-container .content-holder .thumb-img-box .thumb {
        display: inline-block;
        width: 72px;
        height: 72px;
        margin-right: 10px;
        /*background: url('../../../assets/images/components/carousel-example@2x.png');*/
        background-size: cover;
        border: 1px solid rgba(39, 43, 55, 0.2);
        cursor: pointer;
}
.model-detail-container .content-holder .thumb-img-box .thumb .thumb-img {
          width: 70px;
          height: 70px;
}
.model-detail-container .content-holder .thumb-img-box .thumb:last-child {
          margin-right: 0;
}
.model-detail-container .content-holder .thumb-img-box .thumb.selected {
          border: 2px solid #e26829;
          overflow: hidden;
}
.model-detail-container .content-holder .content-box {
      padding: 26px 60px;
}
.model-detail-container .content-holder .content-box .content-item {
        padding: 16px 0;
        border-top: 1px solid rgba(255, 255, 255, 0.1);
        font-size: 0;
}
.model-detail-container .content-holder .content-box .content-item.border-none {
          border: none;
          padding-top: 0;
}
.model-detail-container .content-holder .content-box .content-item .title {
          display: inline-block;
          vertical-align: top;
          width: 140px;
          font-size: 14px;
          color: rgba(255, 255, 255, 0.6);
          padding: 0 5px;
}
.model-detail-container .content-holder .content-box .content-item .title.strong {
            color: #FFFFFF;
            font-weight: bold;
}
.model-detail-container .content-holder .content-box .content-item .desc {
          display: inline-block;
          vertical-align: top;
          width: calc(100% - 140px);
          word-break: break-all;
}
.model-detail-container .content-holder .content-box .content-item .desc.h-24 {
            height: 24px;
}
.model-detail-container .content-holder .content-box .extra-div {
        padding: 16px 0;
        border-top: 1px solid rgba(255, 255, 255, 0.1);
}
.model-detail-container .content-holder .content-box .extra-div .title {
          display: inline-block;
          vertical-align: top;
          width: 140px;
          font-size: 14px;
          color: rgba(255, 255, 255, 0.6);
          padding: 0 5px;
}
.model-detail-container .content-holder .content-box .extra-div .extra .desc {
          display: inline-block;
          vertical-align: top;
          position: relative;
          top: -16px;
}
.model-detail-container .content-holder .content-box .extra-div .extra .desc .fileContent {
            margin-top: 16px;
}
.model-detail-container .content-holder .content-box .extra-div .extra .desc .fileContent .title {
              display: inline-block;
}
.model-detail-container .content-holder .content-box .extra-div .extra .desc .fileContent .btn-group {
              display: inline-block;
              margin-left: 6px;
}
.model-detail-container .content-holder .content-box .extra-div .extra .img-group {
          margin-top: 10px;
}
.model-detail-container .content-holder .content-box .extra-div .extra .img-group img {
            width: 50px;
            height: 50px;
}
.model-detail-container .content-holder .content-box .extra-div .extra .text {
          top: 0;
}
.model-detail-container .el-checkbox-group .el-checkbox {
    margin: 4px 0;
    display: inline-block;
    height: 22px;
    width: 50%;
    padding-left: 10px;
}



