
.report-container {
  /*.content-wrap {*/
  /*display: none;*/
  /*}*/
}
.report-container .header .right {
    float: right;
}
.report-container .header .right .print-btn {
      background: #98B42C;
      border-radius: 100px;
}
.report-container .content .list-group .list-group-item {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    line-height: 20px;
    padding: 6px 30px;
    min-height: 50px;
}
.report-container .content .list-group .list-group-item .label-title {
      width: 300px;
      font-size: 14px;
      color: #A1A5B1;
}
.report-container .content .list-group .list-group-item .content {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      font-size: 14px;
      color: #FFFFFF;
}
.report-container .content .list-group .list-group-item .content .split {
        padding: 0 20px;
}
.report-container .content .list-group .list-group-item .content .error {
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
}
.report-container .content .list-group .list-group-item:nth-child(2n) {
      background: rgba(39, 43, 55, 0.2);
}
.report-container .content .remark-row {
    padding: 6px 30px;
}
.report-container .content .deffect-list {
    padding-bottom: 6px;
    border-bottom: 1px solid #2B2F3B;
}
.report-container .content .deffect-list .error-span {
      color: white;
      margin-right: 10px;
      display: inline-block;
      width: 150px;
}
.report-container .print-content-wrap {
    display: none;
}
.report-container .w-100 {
    width: 150px;
}
.report-container .inline-block {
    display: inline-block;
}
.report-container .pre-dialog {
    width: auto;
}
.report-container .pre-dialog .el-dialog__header {
      display: none;
}
.report-container .pre-dialog .el-dialog__body {
      padding: 0;
}
.report-container .pre-dialog .el-dialog__body > div {
        font-size: 0;
}
.report-container .pre-dialog .pre-img {
      min-width: 100px;
      max-width: 500px;
}
.report-container .img-holder {
    position: relative;
    width: 100px;
    display: inline-block;
    background-color: #FFFFFF;
    margin-right: 10px;
}
.report-container .img-holder .img-content {
      width: 100px;
}
.report-container .img-holder .close-btn {
      width: 15px;
      height: 15px;
      top: 0;
      right: 0;
      position: absolute;
      z-index: 9;
      background: url("../../assets/images/icon-close.png");
}
.report-container .img-holder .close-btn:hover {
        background: url("../../assets/images/icon-close-red.png");
}
.report-container .color-red {
    color: #E26829;
}



