
.video-list {
  width: 100%;
  height: 100%;
}
.video-list .head-text {
    color: #A1A5B1;
    margin: 0 15px;
}
.video-list .label {
    color: white;
    width: 100px;
    display: inline-block;
    margin-right: 15px;
}
.video-list .input {
    width: 270px;
}
.video-list .video-dialog {
    width: 650px;
    z-index: 10000;
}
.video-list .video-dialog .dialog-container {
      position: relative;
      font-size: 0;
}
.video-list .video-dialog .dialog-container .video-iframe {
        position: absolute;
        width: 650px;
        height: 360px;
}



