
.replace-report-holder {
  height: 100%;
}
.replace-report-holder .el-dialog__body {
    padding-bottom: 10px;
}
.replace-report-holder .el-dialog__body .form-item .label {
      display: inline-block;
      width: 100px;
}
.replace-report-holder .el-dialog__body .form-item .input {
      width: 310px;
      margin: 10px;
}
.replace-report-holder .auth-form-item .el-form-item__content .el-checkbox-group .el-checkbox {
    margin: 0;
    width: 50%;
    padding-left: 10px;
}
.replace-report-holder .el-form .auth-title {
    color: #fff;
    padding: 10px 0;
    font-weight: bold;
}
.replace-report-holder .el-form .el-checkbox-group {
    line-height: 30px;
}
.replace-report-holder .el-form .el-checkbox-group .el-checkbox {
      width: 50%;
      padding-left: 20px;
}
.replace-report-holder .el-form .el-checkbox-group .el-checkbox + .el-checkbox {
      margin: 0;
}



