
.service-center-ticket-list {
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.service-center-ticket-list .dz-started {
    display: inline-block;
    width: 120px;
    height: 120px;
}
.service-center-ticket-list .dz-started .dz-message {
      width: 100%;
      height: 100%;
}
.service-center-ticket-list .dz-started .dz-message img {
        width: 120px;
        height: 120px;
}
.service-center-ticket-list .img-list {
    float: left;
}
.service-center-ticket-list .img-list img {
      width: 120px;
      height: 120px;
      margin-right: 10px;
}
.service-center-ticket-list .el-upload-list__item img {
    display: none;
}
.service-center-ticket-list .el-form-item__label {
    color: #A1A5B1;
}
.service-center-ticket-list .table-wrap {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
}
.service-center-ticket-list .header {
    height: auto;
}
.service-center-ticket-list .header .el-form-item {
      margin-bottom: 0;
}



