
.dealer-brand-list {
  height: 100%;
}
.dealer-brand-list .header .btn-accept {
    background-color: #98B42C;
}
.dealer-brand-list .table-wrap .table-content .img {
    background: none;
    height: 18px;
    width: 18px;
    vertical-align: middle;
}
.dealer-brand-list .table-wrap .table-content .span {
    color: #E26829;
    margin-left: 4px;
    display: inline-block;
    vertical-align: middle;
}
.dealer-brand-list .detail-dialog .dialog-header {
    margin: 0 20px;
    padding-bottom: 10px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}
.dealer-brand-list .detail-dialog .dialog-header .title {
      font-size: 14px;
      color: white;
}
.dealer-brand-list .detail-dialog .dialog-content {
    margin: 0 20px;
}
.dealer-brand-list .detail-dialog .dialog-content .item {
      padding: 10px 0;
      border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}
.dealer-brand-list .detail-dialog .dialog-content .item .label {
        margin-left: 10px;
        display: inline-block;
        width: 70px;
        vertical-align: top;
}
.dealer-brand-list .detail-dialog .dialog-content .item .text {
        display: inline-block;
        width: 130px;
        margin-left: 10px;
        color: white;
}
.dealer-brand-list .detail-dialog .dialog-content .item .long {
        width: 300px;
}
.dealer-brand-list .detail-dialog .dialog-content .img {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
}
.dealer-brand-list .detail-dialog .dialog-content .img .label {
        vertical-align: top;
}
.dealer-brand-list .detail-dialog .dialog-content .img .img-box {
        width: 102px;
        height: 102px;
        border: 1px solid #fff;
        margin-left: 10px;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
}
.dealer-brand-list .detail-dialog .dialog-content .img .img-box img {
          max-width: 100px;
          max-height: 100px;
}
.dealer-brand-list .code-dialog .input {
    width: 310px;
}



