
.transition-box {
  border-radius: 4px;
  /*background-color: #409EFF;*/
  text-align: center;
  /*color: #fff;*/
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin-left: 20px;
}
.material-order-detail {
  width: 100%;
  font-size: 18px !important;
  /*height: 100%;*/
  /*.myQR{*/
  /*text-align: center;*/
  /*}*/
}
.material-order-detail .content {
    /*height: 100%;*/
    overflow: auto;
    border: 1px solid #2B2F3B;
}
.material-order-detail .content .order-info .title {
      height: 50px;
      line-height: 50px;
      padding-left: 40px;
      font-size: 18px;
}
.material-order-detail .content .order-info .left {
      float: left;
      /*clear: both;*/
      width: 100%;
      background: #353945;
      -webkit-box-shadow: 0 -1px #2B2F3B;
              box-shadow: 0 -1px #2B2F3B;
}
.material-order-detail .content .order-info .left .item {
        margin-left: 40px;
        line-height: 60px;
        overflow: hidden;
        height: 50px;
}
.material-order-detail .content .order-info .left .item .label {
          display: inline-block;
          width: 174px;
          font-size: 18px;
}
.material-order-detail .content .order-info .left .item span {
          font-size: 18px;
}
.material-order-detail .content .order-info .left .info-list {
        font-size: 0;
}
.material-order-detail .content .order-info .left .info-list .col-left {
          display: inline-block;
          width: 50%;
          /*height:100%;*/
}
.material-order-detail .content .order-info .left .info-list .col-right {
          display: inline-block;
          width: 50%;
          /*height:100%;*/
}
.material-order-detail .content .order-info .left .info-list .gao {
          height: 200px;
}
.material-order-detail .content .order-info .left .info-list .status {
          padding: 5px 10px;
          background-color: #E26829;
          border-radius: 3px 3px;
          font-style: normal;
          color: white;
}
.material-order-detail .content .order-info .left .info-list .list-group {
          padding: 10px 0 0 40px;
}
.material-order-detail .content .order-info .left .info-list .list-group .list-group-item {
            height: 40px;
            line-height: 40px;
            font-size: 0;
}
.material-order-detail .content .order-info .left .info-list .list-group .list-group-item span {
              display: inline-block;
              font-size: 18px;
}
.material-order-detail .content .order-info .left .info-list .list-group .list-group-item span:first-child {
              /*font-size: 12px;*/
              color: #FFFFFF;
              width: 40%;
}
.material-order-detail .content .order-info .left .info-list .list-group .list-group-item span:last-child {
              /*font-size: 12px;*/
              color: #A1A5B1;
              width: 60%;
}
.material-order-detail .content .order-info .left .info-list .list-group .list-group-item .amount {
              width: 100px;
              margin-right: 10px;
}
.material-order-detail .content .order-info .left .info-list .list-group .list-group-item .step span {
              color: white;
              padding-left: -20px;
}
.material-order-detail .content .order-info .left .info-list .list-group .input-sn {
            /*float: left;*/
            width: 235px;
}
.material-order-detail .content .order-info .left .info-list .top-sapce {
          position: absolute;
          top: 0px;
          left: 474px;
          width: 474px;
}
.material-order-detail .content .order-info .left .info-list .top-sapce .btn {
            border-radius: 5px;
            padding: 2px 5px;
}
.material-order-detail .content .order-info .left .info-list .qr-left {
          float: left;
}
.material-order-detail .content .order-info .left .info-list .input-sn:after {
          top: 15px;
}
.material-order-detail .content .order-info .gao2 {
      position: relative;
      height: 190px;
      clear: both;
}
.material-order-detail .content .order-info .parts-list {
      float: left;
      padding: 27px 40px;
      /*font-size: 12px;*/
      overflow: hidden;
      height: 50px;
      width: 100%;
      -webkit-box-shadow: 0 -1px #2B2F3B;
              box-shadow: 0 -1px #2B2F3B;
}
.material-order-detail .content .order-info .parts-list .label1 {
        display: inline-block;
        /*font-size: 12px;*/
}
.material-order-detail .content .footer {
      width: 100%;
      height: 50px;
      padding: 10px 38px;
}
.material-order-detail .content .footer .footer-pagination {
        float: right;
}
.material-order-detail .content .footer .footer-delete {
        margin-top: 3px;
        background-color: #858890;
}
.material-order-detail .dialog .el-dialog--large {
    width: 1100px;
}
.material-order-detail .dialog .header .search-content {
    width: 200px;
    margin-right: 10px;
}
.material-order-detail .dialog .header .search-btn {
    position: absolute;
    top: 15px;
    left: 250px;
    width: 30px;
    height: 30px;
}
.material-order-detail .dialog .table-content {
    padding-bottom: 0;
}



