
.assembler-produce {
  font-size: 16px;
}
.assembler-produce .header-label {
    margin: 0 6px 0 30px;
}
.assembler-produce .content-wrap {
    padding: 0;
}
.assembler-produce .content-wrap .content {
      padding-left: 0;
}
.assembler-produce .content-wrap .content .el-collapse-item__header {
        font-size: 0;
}
.assembler-produce .content-wrap .content .btn-group {
        text-align: right;
        /*padding-bottom: 10px;*/
}
.assembler-produce .content-wrap .content .info {
        font-size: 0;
}
.assembler-produce .content-wrap .content .info .search-item {
          display: inline-block;
          width: 50%;
          font-size: 0;
          padding: 15px 0;
}
.assembler-produce .content-wrap .content .info .search-item .tip-img {
            width: 18px;
            position: relative;
            top: 4px;
            margin-left: 14px;
}
.assembler-produce .content-wrap .content .info .search-item .label {
            display: inline-block;
            width: 150px;
            padding-left: 30px;
}
.assembler-produce .content-wrap .content .info .search-item .scan-input {
            width: 240px;
}
.assembler-produce .content-wrap .content .info .search-item .scan-input .el-input__inner {
              padding-right: 40px;
              text-align: right;
}
.assembler-produce .content-wrap .content .info .search-item .search-btn {
            border-radius: 100px;
            position: relative;
            margin-left: 15px;
}
.assembler-produce .content-wrap .content .info .title-select {
          position: absolute;
          right: 120px;
          top: 16px;
}
.assembler-produce .content-wrap .content .info .title-loading {
          position: absolute;
          display: inline-block;
          width: 30px;
          height: 30px;
          margin-top: 15px;
}
.assembler-produce .content-wrap .content .info .icon {
          display: inline-block;
          width: 20px;
          height: 20px;
          /*line-height: 20px;*/
          vertical-align: middle;
          position: relative;
          top: -4px;
}
.assembler-produce .content-wrap .content .info .title-text {
          display: inline-block;
          padding-left: 10px;
          width: 100px;
}
.assembler-produce .content-wrap .content .info .key {
          width: 120px;
}
.assembler-produce .content-wrap .content .info .title-solution {
          display: inline-block;
          margin-left: 5px;
}
.assembler-produce .content-wrap .content .info .title-sn {
          display: inline-block;
          width: 260px;
}
.assembler-produce .content-wrap .content .info .title-flag {
          display: inline-block;
          /*width: 60px;*/
}
.assembler-produce .content-wrap .content .info .title-progress {
          display: inline-block;
          width: 120px;
          margin-left: 50px;
}
.assembler-produce .content-wrap .content .info > .title {
          position: relative;
          padding-left: 50px;
}
.assembler-produce .content-wrap .content .info > .title li {
            display: inline-block;
            font-size: 14px;
            line-height: 60px;
}
.assembler-produce .content-wrap .content .info > .title li:nth-child(1) {
              width: 100px;
}
.assembler-produce .content-wrap .content .info > .title li:nth-child(2) {
              width: 250px;
}
.assembler-produce .content-wrap .content .info > .title li:nth-child(3) {
              width: 296px;
}
.assembler-produce .content-wrap .content .info > .title li:nth-child(4) {
              width: 160px;
}
.assembler-produce .content-wrap .content .info > .title .service {
            position: absolute;
            right: 0;
            top: 50%;
            margin-top: -13px;
}
.assembler-produce .content-wrap .content .info .info-list-box {
          border-top: 1px solid #22252E;
          border-bottom: 1px solid #22252E;
          padding: 0 30px;
}
.assembler-produce .content-wrap .content .info .info-list-box .header-title {
            font-size: 14px;
            color: #858890;
            margin-bottom: 6px;
}
.assembler-produce .content-wrap .content .info .info-list-box .info-list {
            font-size: 0;
}
.assembler-produce .content-wrap .content .info .info-list-box .info-list .col-left {
              display: inline-block;
              width: 50%;
              text-align: left;
}
.assembler-produce .content-wrap .content .info .info-list-box .info-list .col-right {
              display: inline-block;
              width: 50%;
              text-align: right;
}
.assembler-produce .content-wrap .content .info .info-list-box .info-list .list-group {
              text-align: left;
              margin-top: 20px;
}
.assembler-produce .content-wrap .content .info .info-list-box .info-list .list-group .list-group-item {
                height: 60px;
                font-size: 0;
                display: -webkit-box;
                display: -ms-flexbox;
                display: flex;
                -webkit-box-align: center;
                    -ms-flex-align: center;
                        align-items: center;
                line-height: 20px;
                border-top: 1px solid rgba(255, 255, 255, 0.1);
                border-bottom: none;
}
.assembler-produce .content-wrap .content .info .info-list-box .info-list .list-group .list-group-item span {
                  display: inline-block;
                  font-size: 14px;
}
.assembler-produce .content-wrap .content .info .info-list-box .info-list .list-group .list-group-item span:first-child {
                  color: #FFFFFF;
                  width: 157px;
                  vertical-align: middle;
                  word-wrap: break-word;
}
.assembler-produce .content-wrap .content .info .info-list-box .info-list .list-group .list-group-item span:last-child {
                  padding-left: 5px;
                  color: #A1A5B1;
                  -webkit-box-flex: 1;
                      -ms-flex: 1;
                          flex: 1;
}
.assembler-produce .content-wrap .content .info .info-list-box .info-list .list-group .clear-border-top {
                border-top: none;
}
.assembler-produce .content-wrap .content .info .info-list-box .info-list .list-group.list-all span:first-child {
                width: 157px;
}
.assembler-produce .content-wrap .content .info .info-list-box .info-list .list-group .height {
                display: inline-block;
                vertical-align: middle;
                height: 40px;
}
.assembler-produce .content-wrap .content .info .input-sn {
          width: 240px;
          height: 30px;
          margin-right: 10px;
}
.assembler-produce .content-wrap .content .info .input-sn .el-input__inner {
            padding-right: 40px;
            text-align: right;
}
.assembler-produce .history-table {
    width: 100%;
    /*min-width:700px;*/
    border-top: 1px solid rgba(255, 255, 255, 0.1);
}
.assembler-produce .history-table tr {
      line-height: 30px;
}
.assembler-produce .history-table .head {
      color: white;
}
.assembler-produce .history-table .history-td {
      padding: 0 6px;
      width: 160px;
      border-bottom: 1px solid rgba(255, 255, 255, 0.1);
      font-size: 14px;
      text-align: center;
}
.assembler-produce .progress-wrap {
    display: block;
    height: 30px;
    margin-right: -30px;
}
.assembler-produce .dialog-container-pic {
    text-align: center;
}
.assembler-produce .dialog-container-pic .img {
      width: 100%;
      height: auto;
}
.assembler-produce .error-shooting .content-item {
    width: 100%;
    padding: 16px 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    font-size: 0;
}
.assembler-produce .error-shooting .content-item .title {
      display: inline-block;
      vertical-align: top;
      width: 140px;
      font-size: 14px;
      color: rgba(255, 255, 255, 0.6);
      padding: 0 5px;
}
.assembler-produce .error-shooting .content-item .desc {
      display: inline-block;
      vertical-align: top;
      width: calc(100% - 140px);
}
.assembler-produce .error-shooting .content-item .desc span {
        font-size: 14px;
        color: #FFFFFF;
}
.assembler-produce .error-shooting .content-item .desc span.strong {
          font-weight: bold;
}
.assembler-produce .error-shooting .content-item .desc p {
        font-size: 14px;
        color: #FFFFFF;
}
.assembler-produce .input {
    margin-left: 20px;
    width: 260px;
}
.assembler-produce .connect-tip {
    display: inline-block;
    margin-left: 20px;
}
.assembler-produce .connect-tip a {
      color: #E26829;
}
.assembler-produce .tip-img {
    width: 18px;
    position: relative;
    top: 4px;
}
.assembler-produce .select-holder {
    margin: 20px 10px;
    display: inline-block;
}
.assembler-produce .select-holder .label {
      display: inline-block;
      width: 100px;
      color: white;
      margin-left: 40px;
}
.assembler-produce .bind-battery {
    display: inline-block;
    position: absolute;
    width: 50%;
    right: 0;
    text-align: right;
    padding-right: 90px;
}
.assembler-produce .bind-battery .switch-label {
      margin-right: 24px;
}
.assembler-produce .order {
    border-left: 1px solid #22252E;
    border-right: 1px solid #22252E;
}
.assembler-produce .order .info-list-box {
      border-top: none !important;
      padding: 0 30px;
}
.assembler-produce .btn-connect {
    margin-top: 17px;
}
.assembler-produce .tip {
    display: inline-block;
    border-radius: 50%;
    width: 10px;
    height: 10px;
    background: #E26829;
    margin-left: 6px;
}
.assembler-produce .connect {
    background: #76EE00;
}
.assembler-produce .test {
    overflow: hidden;
    text-align: center;
    margin-top: -38px;
}
.assembler-produce .test .test-btn {
      /*margin-bottom: 10px;*/
      display: inline-block;
      font-size: 24px;
      height: 50px;
      width: 250px;
      border-radius: 25px;
      background-color: #A1A5B1;
      cursor: pointer;
      line-height: 50px;
      font-weight: bold;
      /*&:hover {*/
      /*background-color: #E26829;*/
      /*}*/
}
.assembler-produce .test .test-btn.active {
      background-color: #E26829;
}
.assembler-produce .tip-dialog .el-dialog__body {
    padding: 0;
}
.assembler-produce .history-dialog .el-dialog__body {
    padding-bottom: 10px;
}
.assembler-produce .el-dialog--small {
    width: 770px;
}
.assembler-produce .mr-tpo-0 {
    margin-top: 0 !important;
}
.assembler-produce .no-border-top {
    border-top: none !important;
}
.assembler-produce .width-80 {
    display: inline-block;
    width: 80px;
}
.assembler-produce .width-280 {
    display: inline-block;
    width: 280px;
}



