
.dealer-brand-list {
  height: 100%;
  /**
    #parentdiv{
        //transform:translate(-20px,0);
        //-webkit-transform: translate(-20px,0);
        margin-right:-20px;
    }
    **/
}
.dealer-brand-list .header .btn-accept {
    background-color: #98B42C;
}
.dealer-brand-list .webWrap {
    height: 60px;
    min-width: 500px;
    margin: 0 auto;
    line-height: 60px;
}
.dealer-brand-list .webWrap .input {
      width: 200px;
}
.dealer-brand-list .webWrap .webcontent {
      margin-left: 33px;
      height: 60px;
      line-height: 60px;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}
.dealer-brand-list .webWrap .webcontent .label {
        width: 210px;
        display: inline-block;
        line-height: 20px;
        vertical-align: middle;
        word-wrap: break-word;
}
.dealer-brand-list .webWrap .webcontent .input {
        width: 400px;
}
.dealer-brand-list .webWrap .webcontent > button {
        margin-right: 12px;
        width: 120px;
}
.dealer-brand-list .iframeWrap {
    width: 100%;
    height: 100%;
}
.dealer-brand-list .iframeWrap iframe::-webkit-scrollbar {
      width: 0px;
      height: 0px;
}
.dealer-brand-list .operationWrap {
    margin-top: 20px;
    margin-right: -20px;
    text-align: center;
}
.dealer-brand-list .content-holder .carouselWrap {
    min-width: 800px;
    height: auto;
    overflow: auto;
    background: #fff;
}
.dealer-brand-list .content-holder .carouselWrap .el-carousel {
      width: 800px;
      height: auto;
      margin: 0 auto;
}
.dealer-brand-list .content-holder .carouselWrap .el-carousel .el-carousel__container {
        width: 100%;
        height: 350px;
        /*height: "234px"*/
}
.dealer-brand-list .content-holder .carouselWrap .el-carousel .el-carousel__item .img-box {
        width: 100%;
        height: 100%;
        text-align: center;
}
.dealer-brand-list .content-holder .carouselWrap .el-carousel .el-carousel__item .img-box img {
          width: 500px;
          height: auto;
}
.dealer-brand-list .content-holder .thumb-img-box {
    width: 100%;
    height: 72px;
    font-size: 0;
    text-align: center;
}
.dealer-brand-list .content-holder .thumb-img-box .thumb {
      display: inline-block;
      width: 72px;
      height: 72px;
      margin-right: 10px;
      /*background: url('../../../assets/images/components/carousel-example@2x.png');*/
      background-size: cover;
      border: 1px solid rgba(39, 43, 55, 0.2);
      cursor: pointer;
      overflow: hidden;
}
.dealer-brand-list .content-holder .thumb-img-box .thumb .thumb-img {
        width: 70px;
        height: 70px;
}
.dealer-brand-list .content-holder .thumb-img-box .thumb:last-child {
        margin-right: 0;
}
.dealer-brand-list .content-holder .thumb-img-box .thumb.selected {
        border: 2px solid #e26829;
}



