
.vehicle-search-holder {
  height: 100%;
}
.vehicle-search-holder .content {
    height: 100%;
}
.vehicle-search-holder .content .content-holder {
      padding: 26px 60px;
      overflow: hidden;
}
.vehicle-search-holder .content .content-holder .btn-div {
        display: inline-block;
        position: absolute;
        right: 80px;
}
.vehicle-search-holder .content .content-holder .vehicle-item {
        overflow: hidden;
        padding-bottom: 20px;
}
.vehicle-search-holder .content .content-holder .content-item {
        width: 50%;
        float: left;
        font-size: 0;
        line-height: 50px;
        overflow: hidden;
}
.vehicle-search-holder .content .content-holder .content-item .title {
          display: inline-block;
          vertical-align: top;
          width: 140px;
          font-size: 14px;
          color: rgba(255, 255, 255, 0.6);
          padding-left: 10px;
}
.vehicle-search-holder .content .content-holder .content-item .desc {
          display: inline-block;
          vertical-align: top;
          width: calc(100% - 140px);
}
.vehicle-search-holder .content .content-holder .content-item .desc span {
            font-size: 14px;
            color: #FFFFFF;
}
.vehicle-search-holder .content .content-holder .content-item .desc span.strong {
              font-weight: bold;
}
.vehicle-search-holder .content .content-holder .content-item .desc .input {
            width: 200px;
}
.vehicle-search-holder .content .content-holder .width {
        overflow: hidden;
        width: 100%;
}
.vehicle-search-holder .content .content-holder .bom-content-holder {
        padding: 10px 20px;
}
.vehicle-search-holder .content .content-holder .bom-item {
        width: 100%;
        overflow: hidden;
}
.vehicle-search-holder .content .content-holder .bom-item .bom-header {
          overflow: hidden;
          line-height: 40px;
}
.vehicle-search-holder .content .content-holder .bom-item .bom-header .bom-title {
            font-size: 14px;
            color: white;
            padding-left: 5px;
}
.vehicle-search-holder .content .content-holder .bom-item .bom-header .btn-replace {
            float: right;
            margin-right: 40px;
}
.vehicle-search-holder .content .content-holder .bom-item .bom-content {
          float: left;
          display: inline-block;
          width: 50%;
          font-size: 0;
          line-height: 30px;
          overflow: hidden;
}
.vehicle-search-holder .content .content-holder .bom-item .bom-content .title {
            display: inline-block;
            vertical-align: top;
            width: 140px;
            font-size: 12px;
            color: rgba(255, 255, 255, 0.6);
            padding-left: 10px;
}
.vehicle-search-holder .content .content-holder .bom-item .bom-content .desc {
            display: inline-block;
            vertical-align: top;
            width: calc(100% - 140px);
}
.vehicle-search-holder .content .content-holder .bom-item .bom-content .desc span {
              word-break: break-all;
              font-size: 12px;
              color: #FFFFFF;
}
.vehicle-search-holder .content .content-holder .bom-item .bom-content .desc span.strong {
                font-weight: bold;
}
.vehicle-search-holder .report-table {
    width: 100%;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
}
.vehicle-search-holder .report-table tr {
      line-height: 30px;
}
.vehicle-search-holder .report-table .head {
      color: white;
}
.vehicle-search-holder .report-table .report-td {
      padding: 0 6px;
      width: 160px;
      border-bottom: 1px solid rgba(255, 255, 255, 0.1);
      font-size: 14px;
      text-align: center;
}
.vehicle-search-holder .new-password .input .el-input__inner {
    padding-right: 40px;
}



