
.after-index {
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  min-width: 530px;
  min-height: 360px;
  overflow: auto;
}
.after-index .operation {
    /*flex: 1;*/
    /*height: 0;*/
    /*width: 0;*/
    /*overflow: hidden;*/
}
.after-index .operation .item {
      text-align: center;
      display: inline-block;
      width: 200px;
      margin: 15px;
      border-radius: 10px;
      padding-bottom: 20px;
      cursor: pointer;
      background-color: grey;
}
.after-index .operation .item .order-list {
        height: 160px;
        background: url("../../../assets/images/icon/icon-order.png") no-repeat center;
        background-size: 60px;
}
.after-index .operation .item .order-height {
        height: 128px;
}
.after-index .operation .item .diagnosis {
        height: 160px;
        background: url("../../../assets/images/icon/icon-diagnosis.png") no-repeat center;
        background-size: 80px;
}
.after-index .operation .item .label {
        margin: auto;
        font-size: 26px;
        font-weight: bold;
}
.after-index .operation .active {
      background-color: #98B42C;
}



