
.mechanic-detect {
  font-size: 16px;
}
.mechanic-detect .header.top-box .label-com {
    margin-left: 20px;
    margin-right: 10px;
}
.mechanic-detect .header.top-box .btn-connect {
    margin: 0 8px;
}
.mechanic-detect .header.top-box .tip {
    display: inline-block;
    border-radius: 50%;
    width: 10px;
    height: 10px;
    background: #E26829;
}
.mechanic-detect .header.top-box .connect {
    background: #76EE00;
}
.mechanic-detect .header.top-box .tip-img {
    width: 18px;
    position: relative;
    top: 4px;
}
.mechanic-detect .content-wrap {
    padding: 10px 0;
}
.mechanic-detect .content-wrap .content {
      padding-left: 0;
}
.mechanic-detect .content-wrap .content .el-collapse-item__header {
        font-size: 0;
}
.mechanic-detect .content-wrap .content .info .search-item {
        width: 100%;
        /*font-size: 0;*/
}
.mechanic-detect .content-wrap .content .info .search-item .tip-img {
          width: 18px;
          position: relative;
          top: 4px;
          margin-left: 14px;
}
.mechanic-detect .content-wrap .content .info .search-item .label {
          display: inline-block;
          width: 100px;
          margin-left: 22px;
}
.mechanic-detect .content-wrap .content .info .search-item .search-btn {
          border-radius: 100px;
          position: relative;
          margin-left: 15px;
}
.mechanic-detect .content-wrap .content .info .title-select {
        /*position: absolute;*/
        /*right: 120px;*/
        /*top: 16px;*/
}
.mechanic-detect .content-wrap .content .info .title-loading {
        position: absolute;
        display: inline-block;
        width: 30px;
        height: 30px;
        margin-top: 15px;
}
.mechanic-detect .content-wrap .content .info .icon {
        display: inline-block;
        width: 20px;
        height: 20px;
        /*line-height: 20px;*/
        vertical-align: middle;
        position: relative;
        top: -4px;
}
.mechanic-detect .content-wrap .content .info .title-text {
        display: inline-block;
        margin-left: 10px;
        width: 100px;
}
.mechanic-detect .content-wrap .content .info .title-flag {
        display: inline-block;
        /*width: 60px;*/
}
.mechanic-detect .content-wrap .content .info .input-sn {
        width: 240px;
        height: 30px;
        margin-left: 30px;
}
.mechanic-detect .content-wrap .content .info .input-sn .el-input__inner {
          padding-right: 40px;
          text-align: right;
}
.mechanic-detect .content-wrap .content .info .component-list ul li {
        position: relative;
        height: 60px;
        line-height: 60px;
        padding-left: 20px;
        border-top: 1px solid #22252E;
}
.mechanic-detect .content-wrap .content .info .component-list ul li .title-select {
          position: absolute;
          right: 120px;
          top: 16px;
}
.mechanic-detect .content-wrap .content .info .component-list ul li .el-textarea {
          width: 810px;
          vertical-align: middle;
}
.mechanic-detect .content-wrap .content .parts-list {
        position: relative;
        height: 50px;
        line-height: 50px;
}
.mechanic-detect .content-wrap .content .parts-list .title {
          padding-left: 55px;
          font-size: 16px;
}
.mechanic-detect .content-wrap .content .parts-list .search {
          float: right;
          padding-right: 30px;
}
.mechanic-detect .content-wrap .content .parts-list .search .input-sn:after {
            top: 15px;
}
.mechanic-detect .content-wrap .content .table-wrap {
        border: 1px solid #2A2E3A;
}
.mechanic-detect .content-wrap .content .table-wrap .table-footer {
          position: static;
}
.mechanic-detect .content-wrap .content .el-collapse-item__header {
        height: 60px;
        line-height: 60px;
}
.mechanic-detect .content-wrap .content .el-collapse-item__header .el-collapse-item__header__arrow {
          top: 24px;
}
.mechanic-detect .dialog-container-pic {
    text-align: center;
}
.mechanic-detect .dialog-container-pic .img {
      width: 100%;
      height: auto;
}
.mechanic-detect .error-shooting .content-item {
    width: 100%;
    padding: 16px 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    font-size: 0;
}
.mechanic-detect .error-shooting .content-item .title {
      display: inline-block;
      vertical-align: top;
      width: 140px;
      font-size: 14px;
      color: rgba(255, 255, 255, 0.6);
      padding: 0 5px;
}
.mechanic-detect .error-shooting .content-item .desc {
      display: inline-block;
      vertical-align: top;
      width: calc(100% - 140px);
}
.mechanic-detect .error-shooting .content-item .desc span {
        font-size: 14px;
        color: #FFFFFF;
}
.mechanic-detect .error-shooting .content-item .desc span.strong {
          font-weight: bold;
}
.mechanic-detect .error-shooting .content-item .desc p {
        font-size: 14px;
        color: #FFFFFF;
}
.mechanic-detect .print-content-wrap {
    display: none;
}
.mechanic-detect .tip-dialog .el-dialog__body {
    padding: 0;
}



