
.dealer-order-detail {
  height: 100%;
}
.dealer-order-detail .loading {
    height: 150px;
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.dealer-order-detail .content .title {
    display: block;
    margin-top: 15px;
    padding: 9px 33px;
    font-size: 18px;
}
.dealer-order-detail .content .info {
    margin-top: 15px;
    background-color: #353945;
    border-bottom: 1px dotted #2B2F3B;
    overflow: hidden;
}
.dealer-order-detail .content .info .left {
      float: left;
      width: 465px;
}
.dealer-order-detail .content .info .left .item {
        margin-left: 33px;
        line-height: 50px;
        overflow: hidden;
        height: 50px;
}
.dealer-order-detail .content .info .left .item .label {
          display: inline-block;
          width: 120px;
}
.dealer-order-detail .content .info .left .item .text {
          color: #858890;
}
.dealer-order-detail .content .info .right {
      float: left;
      width: 465px;
}
.dealer-order-detail .content .info .right .item {
        margin-left: 33px;
        line-height: 50px;
        overflow: hidden;
        height: 50px;
}
.dealer-order-detail .content .info .right .item .label {
          display: inline-block;
          width: 120px;
}
.dealer-order-detail .content .info .right .item .text {
          color: #858890;
}
.dealer-order-detail .content .update-invoice {
    margin-left: 30px;
    margin-top: 15px;
}
.dealer-order-detail .content .update-invoice .invoice-input {
      width: 260px;
}
.dealer-order-detail .content .update-invoice .btn {
      margin-left: 30px;
}
.dealer-order-detail .table {
    width: calc(100% - 66px);
    margin: 10px 33px 20px 33px;
    border-right: 1px solid #848796;
    border-bottom: 1px solid #848796;
}
.dealer-order-detail .table tr {
      line-height: 30px;
}
.dealer-order-detail .table tr td {
        text-align: center;
        border-left: 1px solid #848796;
        border-top: 1px solid #848796;
}



